/*import State from './State';
import Observer from './Observer'*/

class Observer {
    constructor() {
      this.handlers = [];
    }
  
    subscribe(func) {
      this.handlers.push(func);
    }
  
    unsubscribe(func) {
      this.handlers = this.handlers.filter(
        function (item) {
          item !== func ? item : false;
        }
      )
    }
  
    deliver(state) {
      this.handlers.forEach(func => {
        func(state);
      });
  
      return this;
    }
  } 

class Storage {
  constructor(observer) {
    this.state = {};
    this.IDIterator = 0;
    this.observer = observer;
  }

  add(item) {
    const id = this.IDIterator++;
    item ? this.state[id] = item : false;
    this.observer.deliver(this);
  }

  edit(id, value) {
    this.state[id] ? this.state[id] = value : false;
    this.observer.deliver(this);
  }

  remove(id) {
    this.state[id] ? delete this.state[id] : false;
    this.observer.deliver(this);
  }

  getState() {
    return this.state;
  }
};

class Main {
  constructor(observer, state) {
    const root = document.getElementById('root');

    const controls = document.createElement('div');
    const input = document.createElement('input');
    const button = document.createElement('input');

    const list = document.createElement('div');
    const items = document.createElement('ul');

    input.setAttribute('placeholder', 'Enter test');
    button.setAttribute('value', 'ADD');
    button.setAttribute('type', 'button');
    input.setAttribute('id', 'input');

    controls.appendChild(input);
    controls.appendChild(button);

    list.setAttribute('id', 'list-todo-list');
    list.appendChild(items);

    root.appendChild(controls);
    root.appendChild(list);

    this.observer = observer;
    this.controls = controls;
    this.list = list;
    this.input = input;

    //subscribe
    observer.subscribe(this.render.bind(this)); //set this for render method, otherwise we lose the context

    ///add listeners
    button.onclick = () => {
      const { value } = input;
      //check on emty field
      value.length ? state.add(value)
        : alert('Please enter at least one character');
        
    }
    list.onclick = event => {
      const id = event.target.parentElement.dataset.id;
      if (event.target.nodeName === "I" && event.target.className === 'fas fa-times close') {
        //remove item from object
        state.remove(id);
      }
      else if (event.target.nodeName === "I" && event.target.className === 'far fa-edit edit') {
        const result = prompt("Ð¡hange the task, please", event.target.parentElement.textContent);
        if (result.length > 0) {
          //update Task
          state.edit(id, result);
        }
        else { alert('Empty string') }
      }
    }

  }

  render(state) {
    this.list.innerHTML = "";
    this.input.value = ""; //clear input value

    for (const id in state.state) {
      const value = state.state[id];
      const li = document.createElement('li');
      li.setAttribute('data-id', id);
      li.innerHTML = `<span class = "text-of-task">${value}</span><i class="far fa-edit edit"></i><i class="fas fa-times close"></i>`;
      this.list.appendChild(li);
    }
  }
};


  const observer = new Observer();
  const state = new Storage(observer); 
  new Main(observer,state);



